$color-primario:rgb(96, 41, 133);
* {
    margin: 0;
    padding: 0;
    background-color: rgb(0, 0, 0);
}
img {
    max-width: 100%;
    height: auto;
    transform: scale(1);
    transition: transform .50s;
}
img:hover{ 
    transform: scale(1.1);
    filter:saturate(1.5)

}

.contenedor {
    max-width: 1200px;
    margin: auto;
    display: grid;
    grid-template-columns: 20% 80% ;
    grid-template-rows: 70px 1fr 50px;
    grid-template-areas: 
        "navegacion encabezado "
        "navegacion  principal"
        "navegacion pie"
    ;
}

header {
    grid-area: encabezado;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $color-primario;
    font-size: 30px;
    font-style: oblique;
}
nav {
    grid-area: navegacion;
    padding: 5px;
}
nav img {
    display: block;
    margin: auto;
    border-radius: 20px;
}
nav ul {
    list-style: none;
    margin-top: 10px;

    li a {
        background: $color-primario;
        display: block;
        text-align: center;
        text-decoration: none;
        text-transform: uppercase;
        font-size: 25px;
        font-family: Cambria;
        font-style: oblique;
        padding: 10px;
        margin-bottom: 5px;
        color: rgb(255, 255, 255);
        border-radius: 5px;
        &:hover{
            background-color: black;
        }
    }
}

main{
    grid-area: principal;
}

table{
    color: rgb(233, 160, 2);
    background-color: $color-primario;
    font-family: Cambria;
    font-size: 20px;
    text-align: center;
    margin: 20px;
    padding: 2px;
    width: 96%;
}
h2{
    font-style: oblique;
    color: rgb(255, 255, 255);
    background: $color-primario;
    border-radius: 3px;
}
footer {
    padding: 10px;
    grid-area: pie;
    width: 120px ;
}

#galeria{ 
    text-align: center;
    margin: 1px;  
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5px;
}

#multimedia{
    text-align: center;
    margin: 20px;
    display: grid;
    grid-template-columns: repeat(1, 1f);
}
#redes{
    display: flex;
    background: rgb(96, 41, 133);
}

/* TABLET */
@media (max-width: 1024px) {
    .contenedor {
        grid-template-columns: 1fr;
        grid-template-rows: 70px 100px 1fr 50px;
        grid-template-areas:
            "navegacion  "
            "encabezado"
            "principal "
            "pie "

        ;
    }
    section {
        padding: 10px
    }
    nav ul,
    nav {
        display: flex;
    }
    nav ul li a{
        font-size: 23px;
        margin: 1px;
    }
    nav img{
        width:18%;
    }

    #galeria {
        grid-template-columns: repeat(2, 1fr);
    }
   

}

/* MOVIL */
@media (max-width: 767px) {
    #galeria {
        grid-template-columns: 1fr;
    }
    nav ul li a{
        font-size: 18px;
    }
}
